<template>
  <q-layout class="default-layout">
    <app-header />
    <q-page-container :key="Number(!showChannelsModal)" class="main-box">
      <router-view />
    </q-page-container>

    <p class="footer-desc">Ⓒ 2022 - {{ ( (new Date()).getFullYear() ) }}, Beenow</p>

    <div class="toTopButton" :class="{ active: showScrollBtn }" @click="scrollToTop">
      <img src="@/assets/img/arrow.svg" width="24" height="24" alt="Вгору" />
      <span v-if="newPostsCount !== 0" class="new-posts-count">
        {{ newPostsCount > 9 ? "10+" : newPostsCount }}
      </span>
    </div>

    <div v-if="showCookiePanel" class="cookiePanel">
      <p>
        {{ $t("cookie_message") }}
        <br />
        <br />
        {{ $t("thanks") }}!
      </p>
      <button class="close-btn" title="Закрити" @click="closeCookiesPanel">
        +
      </button>
    </div>
  </q-layout>
</template>

<script>
import { ref, computed, defineComponent } from "vue"
import { useStore } from "vuex"
import AppHeader from "@/components/AppHeader.vue"
import { StorageService, PreloaderService } from "@/services"
import { gridImg } from "@/files"
import { toggleBodyScroll } from "@/utils"

export default defineComponent({
  name: "MainLayout",
  components: {
    AppHeader,
  },
  setup() {
    const showCookiePanel = ref(true)
    const showChannelsModal = ref(false)

    const store = useStore()

    const storageService = new StorageService()
    const { scrollToTop, showScrollBtn } = PreloaderService()

    const isHideCookiePanel = storageService.get("isHideCookiePanel")
    const newPostsCount = computed(() => store.getters["post/newPostsCount"])


    if (isHideCookiePanel) {
      showCookiePanel.value = false
    }

    const channelModalToggle = async (isShow) => {
      if (!isShow) {
        await store.dispatch("post/loadPosts")
      }

      showChannelsModal.value = isShow

      toggleBodyScroll(isShow)
    }

    const closeCookiesPanel = () => {
      showCookiePanel.value = false
      storageService.set("isHideCookiePanel", true, 1.21e6 * 1000)
    }

    return {
      showCookiePanel,
      showChannelsModal,
      gridImg,
      newPostsCount,
      scrollToTop,
      showScrollBtn,
      closeCookiesPanel,
      channelModalToggle,
    }
  }
})
</script>

