  height: 100%;
}

.container {
  position: relative;
  max-width: 450px;
  width: 100%;
  margin: 0 auto;

  @media (min-width: 450px) and (max-width: 700px) {
    max-width: 650px;
    padding: 0 15px;
  }
}

.main-box {
  display: flex;
  flex-direction: column;
  min-height: calc(100% - 126px);
}

.channels-modal {
  .close-btn {
    position: fixed !important;
  }
}

.footer-desc {
  font-size: 14px;
  color: #000;
