            {{ $t('about_project') }}
          </button>
        </div>
      </div>
      <div v-show="isHomePage" class="lists-tab lists-tab-slider-box slider">
        <div id="lists-tab-slider">
          <div class="swiper-wrapper">
            <div v-for="list in lists" :key="list.id" class="swiper-slide">
              <button
                :title="list.title"
                class="list-tab-button"
                :class="{ active: list.name === activeListName }"
                @click="changeActiveCategory(list.name)"
              >
                {{ list.title }}
              </button>
            </div>
          </div>
        </div>
      </div>
