<script>
import { ref, computed, onMounted, onUnmounted, nextTick, watch } from 'vue';
import { useRouter, useRoute } from 'vue-router';
import { toggleBodyScroll } from '@/utils';
import Swiper from 'swiper';
import 'swiper/swiper-bundle.css';

const useHeader = () => {
  const activeListName = ref(localStorage.getItem('category') || 'all');
  const router = useRouter();
  const route = useRoute();
  const lists = ref([]);

  const isHomePage = computed(() => route.path === '/');
  const refreshPage = () => {
    if (!isHomePage.value) {
      return;
    }

    location.reload();
  };

  const changeActiveCategory = (name) => {
    activeListName.value = name;
    window.app.methods.changeActiveCategory(name);
  };

  const showModal = ref(false);

  const modal = (isShow) => {
    showModal.value = isShow;
    toggleBodyScroll(isShow);
  };

  const swiper = ref();

  const loadListsSlider = async () => {
    if (!isHomePage.value) {
      return;
    }

    if (swiper.value) {
      nextTick(() => {
        swiper.value.update();
      });

      return swiper.value;
    }

    return new Swiper('#lists-tab-slider', {
      slidesPerView: 'auto',
      loop: false,
      centeredSlides: false,
      spaceBetween: 10,
      freeMode: true,
    });
  };

  const loadHeader = async () => {
    if (!isHomePage.value) {
      return;
    }

    setTimeout(async () => {
      await window.app.methods.setCategoryLists(lists);

      swiper.value = await loadListsSlider();
    }, 10);
  };

  onMounted(() => {
    nextTick(async () => {
      await loadHeader();
    });
  });

  watch(isHomePage, async () => {
    await loadHeader();
  });

  onUnmounted(() => {
    if (swiper.value) {
      swiper.value.destroy();
    }
  });

  return {
    activeListName,
    isHomePage,
    lists,
    router,
    refreshPage,
    changeActiveCategory,
    modal,
    showModal,
    darkLogo: require('@/assets/img/dark-logo.svg'),
  };
};

export default {
  name: 'AppHeader',
  setup() {
    return useHeader();
  },
};
</script>

